.B {
  padding: 8px 0;

  background: #2C3E50;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FD746C, #2C3E50);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#FD746C), to(#2C3E50));
  background: -webkit-linear-gradient(left, #FD746C, #2C3E50);
  background: -o-linear-gradient(left, #FD746C, #2C3E50);
  background: linear-gradient(to right, #FD746C, #2C3E50); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.B-item:first-child {
  margin-top: 0;
}

.B-item {
  margin: 8px;
  height: 16px;
}




