.A {
  min-height: 200px;
  width: 100%;
  height: 100%;

  background: #ffd89b;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #19547b, #ffd89b);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#19547b), to(#ffd89b));
  background: -webkit-linear-gradient(right, #19547b, #ffd89b);
  background: -o-linear-gradient(right, #19547b, #ffd89b);
  background: linear-gradient(to left, #19547b, #ffd89b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}




